@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    background-color: v.$gris-claro; 

    @include m.tablet {
        display: flex;
        height: 100vh;        
    }

    .principal{
        flex:1;
        overflow: auto;
    }

    .formulario{
        input:not([type="submit"]){
            background-color: v.$blanco;
        }
        input[type="submit"]{
            @include m.boton(v.$indigo,v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.4rem;
            border-radius: .5rem;
        }
    }   
}

.contenido{
    padding: 2rem;
    .nombre-pagina{
        font-size: 2.8rem;
        text-align: center;
    }
}


.enlace{
    color:v.$indigo;
    &:hover{
        color:darken(v.$indigo,20%);
    }
}