html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #b7d6e5;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  margin: 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2.2rem;
}

.nombre-pagina {
  margin: 1rem 0;
  text-align: center;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.header {
  padding: 1rem 10rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #0da6f3;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}

.imagen__contenedor {
  height: 15rem;
  width: 15rem;
  border-radius: 2rem;
  background-color: #ffffff;
}

.imagen__contenedor--transparente {
  height: 15rem;
  width: 15rem;
  border-radius: 2rem;
  background-color: #0da6f3;
}

.imagen {
  margin: 0 auto;
  height: 14rem;
  width: 14rem;
  background-image: url("../img/2.png");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

.footer {
  position: fixed;
  left: 0px;
  bottom: 0px;
  width: 100vw;
  background-color: #0da6f3;
  padding-top: 1rem 0;
  color: #1a1b15;
}
.footer__copyright {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 900;
}
.footer__copyright--regular {
  font-weight: 400;
}

.auth {
  width: min(95%, 80rem);
  margin: 0 auto;
  border: 1px solid #7f8ea3;
  border-radius: 1rem;
  padding: 1rem;
  margin: 1rem auto;
  background-color: #ffffff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  margin-top: 5rem;
}
.auth__texto {
  font-size: 2rem;
  text-align: center;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 0.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #ffffff;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba #1a1b15, 0.8;
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  padding: 1rem;
  border: 1px solid darken #F8FAFC, 20;
  border-radius: 0.5rem;
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #0da6f3;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.8rem;
  font-weight: 700;
  color: #1a1b15;
}
.formulario__submit {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  border-radius: 1rem;
  width: auto;
  align-self: center;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}

.acciones {
  margin-top: 1rem;
  margin-bottom: 3rem;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #0da6f3;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #ffffff;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #ffcbcb;
  border: 0.2rem solid #cb0000;
  border-radius: 1rem;
  color: #cb0000;
}
.alerta.exito {
  background-color: #9aff6c;
  border: 0.2rem solid #329f00;
  border-radius: 1rem;
  color: #123900;
}

.dashboard {
  background-color: #F8FAFC;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: auto;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #ffffff;
}
.dashboard .formulario input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 2rem 0;
  border-radius: 1rem;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.4rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #352ca3;
  cursor: pointer;
}

.contenido {
  padding: 2rem;
}
.contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}

.enlace {
  color: #4338CA;
}
.enlace:hover {
  color: #28217b;
}

.sidebar {
  opacity: 0;
  background-color: #007df4;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  padding: 2rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.85);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #007df4;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #ffffff;
  margin: 0;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #ffffff;
  display: block;
  margin-bottom: 2rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  font-size: 2.4rem;
  text-transform: uppercase;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #28217b;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.4rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}

.barra-mobile {
  background-color: #4338CA;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
