.barra-mobile h1 {
  color: #ffffff;
  margin: 0;
}
.barra-mobile .menu img {
  width: 0.8rem;
}
.barra-mobile .menu img:hover {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
