}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #0063c1;
  cursor: pointer;
}

.no-proyectos a {
  color: #4338CA;
}

.listado-proyectos {
  list-style-type: none;
